.teacher-control {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    .materials_group_header {
        .teacher-control__add-course-teachers__btn {
            &::before {
                background: center / cover no-repeat url("../img/icons/Edit.svg") !important; } } }

    &-messages {
        &__payeer {
            display: flex;
            flex: 0 0 100%;
            &-img {
                font-size: 10px;
                width: 4.5em;
                height: 4.5em;
                border-radius: 50%;
                overflow: hidden;
                margin-right: 15px;
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover; } } }
        .c_nav {
