                    position: absolute;
                    font-size: 10px;
                    width: 1.8em;
                    height: 1.8em;
                    top: 5px;
                    left: 0;
                    background: center / cover no-repeat url("../img/icons/edit.svg"); } }
            .card__btn-go-to {
                font-weight: 500;
                font-size: 16px;
                line-height: 1.68;
                color: #F47920;
                position: relative;
                padding-left: 28px;
                margin-right: 60px;
                &::before {
                    content: "";
                    position: absolute;
                    font-size: 10px;
                    width: 1.8em;
                    height: 1.8em;
                    top: 5px;
                    left: 0;
                    background: center / cover no-repeat url("../img/icons/goto.svg"); } }
            .card__btn-archive {
                padding-left: 28px;
                font-weight: 500;
                font-size: 16px;
                line-height: 1.68;
                color: #FF3838;
                position: relative;
