                left: -20px;
                top: -3px;
                border-radius: 2px; } } }
    &__content {
        margin-top: -24px;
        flex: 0 0 73%;
        // display: flex
        // flex-direction: column
        // flex-wrap: wrap
        .btn_create_course {
            width: 236px;
            height: 65px;
            text-align: center;
            margin-top: 20px; }
        .pagination {
            margin: auto; } }
    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 33px;
        &-img {
            margin-right: 20px;
            width: 70px;
            height: 70px;
            border-radius: 100%;
            overflow: hidden;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover; } }
        &-name {
            font-size: 24px;
            font-weight: 600;
            line-height: 1.375; } }
    &__nav {
        box-shadow: 0px 22px 145px rgba(157, 160, 184, 0.1), 0px 2.75474px 18.1562px rgba(151, 156, 193, 0.07);
        background-color: #fff;
        border-radius: 20px;
        padding-top: 40px;
        padding-left: 40px;
        padding-bottom: 30px;
        flex: 0 0 23.5%;
        overflow: hidden;
        margin-bottom: 35px;
        &-list {
            &-item {
                margin-bottom: 35px;
                &:last-child {
                    margin-bottom: 0; }
                &-link {
                    color: #333333;
                    text-decoration: none;
                    font-size: 20px;
                    line-height: 1.7;
                    width: 100%;
                    display: inline-block;
                    position: relative;
                    &::after {
                        content: "";
                        position: absolute;
                        right: 0;
                        top: 50%;
                        transform: translateY(-50%);
                        border-radius: 50px;
                        display: block;
                        height: 68px;
                        width: 3px;
                        background-color: $blue;
                        opacity: 0;
                        transition: .3s ease opacity; }
                    &:hover {
                        text-decoration: none;
                        &::after {
                            opacity: 1; } }
                    &--active {
                        color: #168EC4;
                        font-weight: 700;
                        &::after {
                            content: "";
                            position: absolute;
                            right: 0;
                            top: 50%;
                            opacity: 1;
                            transform: translateY(-50%);
                            border-radius: 50px;
                            display: block;
                            height: 68px;
                            width: 3px;
                            background-color: $blue; } } } } } }
    &__personal {
        &-title {
            font-weight: 600;
            font-size: 40px;
            line-height: 1.4;
            margin-bottom: 5px; } } }


.personal-info {
    &-title {
        font-size: 20px;
        font-weight: 400;
        margin-bottom: 10px;
        margin-top: 0; }
    &-input-iconed {
