@media (max-width: 1430px) {
    .teacher-control {
        &__nav {
            padding-left: 30px;
            &-list-item {
                margin-bottom: 20px; } }
        &__header-name {
            font-size: 20px; }
        &__header-img {
            margin-right: 10px;
            width: 60px;
            height: 60px; } } }

@media (max-width: 1200px) {
    .teacher-control {
        &__nav {
            &-list-item-link {
                font-size: 17px;
                line-height: 1.35; } } } }


@media (max-width: 992px) {
    .teacher-control {
        display: block;

        &__header {
            margin-bottom: 0;
            width: 40px;
            margin-right: 25px;

            &-img {
                width: 40px;
                height: 40px;
                margin-right: 0; }
            &-name {
                display: none; } }
        &__nav {
            border-radius: 5px;
            padding: 0px 0px 0px 30px;
            display: flex;
            align-items: center;

            &-block {
                position: relative;
                width: 100%;
                width: calc(100% - 65px);
                padding-right: 20px;


                &:after {
                    content: "";
                    position: absolute;
                    right: 0;
                    top: 0;
                    bottom: 0;
                    width: 45px;
                    background: linear-gradient(90deg, rgba(255,255,255,0), #FFF); } }

            &-list {
                white-space: nowrap;
                overflow-x: auto;
                scrollbar-color: transparent transparent;
                scrollbar-width: 0px;

                &::-webkit-scrollbar {
                    width: 0;
                    height: 0; } }

            &-list-item {
                display: inline-block;
                margin-right: 20px;
                margin-bottom: 0;

                &-link {
                    display: block;
                    padding: 20px 0;
                    width: auto;
                    position: static;

                    &:after {
                        content: none; } } } } } }



@media (max-width: 640px) {
    .teacher-control {
        &__personal-title {
            font-size: 30px; }
        &__header-text {
            margin-top: 10px; } }

    .teacher-control__add-course-general-fluid-btn-block {
        flex-wrap: wrap;

        &-wrap {
            flex: 0 0 100%; }

        .teacher-control__btn-cancel {
            order: 1;
            flex: 0 0 100%;
            width: 100%;
            margin-top: 10px; }

        .teacher-control__btn-save {
            // flex: 0 0 calc(60% - 10px)
            width: calc(60% - 10px);
            margin-right: 10px; }

        .popup-with-form {
            // flex: 0 0 40%
            width: 40%;
            margin-left: 0 !important; } } }


