        margin-bottom: 30px; } }

.courses-item {
    display: flex;
    margin-bottom: 30px;
    border-radius: 10px;
    background: #FFF;
    box-shadow: 0px 0px 40px rgba(0,0,0, 0.07);
    @include fz(16);
    &-img {
        flex: 0 0 300px;
        width: 100%;
        max-width: 34%;
        img {
            display: block;
            border-radius: 10px 0px 0px 10px; } }
    &-body {
        width: 100%;
        padding: 25px 30px 70px 30px;
        position: relative; }
    &-title {
        font-weight: 700;
        @include fz(24);
        margin-bottom: 10px;
        line-height: 1.3;
        a {
            color: $font;
            &:hover {
                color: $primary; } } }

    &-descr {
        margin-bottom: 15px; }
    &-manage {
        position: absolute;
        left: 30px;
        right: 30px;
        bottom: 20px;
        &-link {
            display: inline-block;
            padding-left: 26px;
            margin-right: 30px;
            margin-bottom: 5px;
            position: relative;
            color: $secondary;
            font-weight: 500;

            &:hover {
                color: $secondary_hover; }
            &:last-child {
                margin-right: 0; }
            &.red {
                color: #FF3838;
                &:hover {
                    color: darken(#FF3838, 10%); } } }
        &-icon {
            position: absolute;
            left: 0;
            top: 2px;
            width: 18px;
            height: 18px;
            background-position: center;
            background-repeat: no-repeat;
            background-size: contain; } }

    @media (max-width: 1100px) {
        &-body {
            padding-bottom: 15px; }
        &-manage {
            position: static; } }

    @media (max-width: 768px) {
        box-shadow: none;

        &-img {
            flex: 0 0 120px;
            max-width: 120px;
            img {
                border-radius: 5px; } }
        &-body {
            padding: 0px 0px 0px 15px; }
        &-title {
            @include fz(20); } }
    @media (max-width: 420px) {
        display: block;
        box-shadow: 0px 0px 40px rgba(0,0,0, 0.07);
        padding: 15px;

        &-img {
            max-width: 100%;
            margin-bottom: 15px;
            img {
                margin: 0 auto; } }
        &-body {
            padding: 0; }
        &-title {
            @include fz(18); } } }

.icon-manage {
    &-edit {
        background-image: url(../img/icons/edit.svg); }
    &-to {
        background-image: url(../img/icons/goto.svg); }
    &-archive {
        background-image: url(../img/icons/archive.svg); } }


.education-cards {
    .card_item {
        margin-bottom: 30px;
        min-height: calc(100% - 30px); } }
