// Заголовок элемента формы
.customer-subtitle {
    font-weight: 500;
    font-size: 20px;
    line-height: 1.7;
    color: #262626; }

// Заголовок блока
.teacher-block-title {
    font-weight: 600;
    line-height: 1.4;
    color: #262626; }
// маленький серый текст
.teacher-grey-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 1.68;
    color: #8d8d8d; }
// Средний черный текст
.teacher-black-text-m {
    font-weight: 600;
    font-size: 20px;
    line-height: 1.4;
    color: #262626; }
// Обычный черный текст
.teacher-black-text-r {
    font-weight: 600;
    font-size: 18px;
    line-height: 1.38;
    color: #262626; }
// Обычный черный текст 28px
.teacher-black-text-r-28 {
    font-weight: 600;
    font-size: 28px;
    line-height: 1.42;
    color: #262626; }
// Тонкий черный текст
.teacher-black-text-s {
    font-weight: 400;
    font-size: 18px;
    line-height: 1.38;
    color: #333333; }
// Серая кнопка
.teacher-grey-btn {
    font-weight: 700;
    font-size: 16px;
    line-height: 1.25;
    color: #7a89a6;
    background-color: #f6f8fc;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.2s linear all;
    text-decoration: none !important;
    border: none;
    &:hover {
        background-color: darken(#f6f8fc,5% );
        color: #7a89a6; } }
// Синяя кнопка
.teacher-blue-btn {
    font-weight: 700;
    font-size: 16px;
    line-height: 1.25;
    color: #ffffff;
    background-color: #168ec4;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.2s linear all;
    text-decoration: none !important;
    border: none;
    &:focus {
        color: #ffffff; }
    &:hover {
        background-color: darken(#168ec4,5% );
        color: #ffffff; } }
// Кнопка посмотреть
.teacher-watch-btn {
    position: relative;
    font-weight: 500;
    font-size: 18px;
    line-height: 1.66;
    color: #F47920;
    text-decoration: none;
    border: none;
    padding-left: 36px;
    outline: none;
    text-decoration: none !important;
    background-color: transparent;
    &:focus {
        color: #F47920; }

    &:hover {
        color: #F47920; }
    &::before {
        content: "";
        position: absolute;
        font-size: 10px;
        width: 2.6em;
        height: 2.6em;
        left: 0;
        top: 0;
        background: center / cover no-repeat url("../img/icons/teacher-watch.svg"); } }
// Купленный товар
.teacher-good-link {
    padding-left: 28px;
    position: relative;
    color: #168EC4;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.66;
    text-decoration: none;
    background-color: transparent;

    &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        transform: translateY(40%);
        font-size: 10px;
        width: 1.8em;
        height: 1.8em;
        background: center / cover no-repeat url("../img/icons/teacher-cart.svg"); } }
// Иконка истории денежных средств
.teacher-history-icon {
    font-size: 10px;
    width: 3em;
    height: 3em;
    img {
        width: 100%;
        height: 100%;
        object-fit: cover; } }
// Сумма вывода средств
.withdraw-current {
    font-weight: 600;
    font-size: 50px;
    line-height: 1.4;
    color: #168EC4; }
// Поля для ввода информации ученика
.customer-input-group {
    overflow: hidden;
    height: 64px;
    input {
        height: 100%;
        width: 100%;
        border: 1px solid #DFDEE2;
        border-radius: 5px;
        background-color: #fff;
        padding: 16.5px 20px;
        color: #8D8D8D;
        font-weight: 400;
        font-size: 16px;
        line-height: 1.6;
        &::placeholder {
            color: #8D8D8D;
            font-weight: 400;
            font-size: 16px;
            line-height: 1.6; } } }
// Поле для ввода с иконкой
.customer-input-group-with-icon {
    overflow: hidden;
    display: flex;
    border: 1px solid #DFDEE2;
    border-radius: 5px;
    width: 100%;
    &__icon {
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 5px;
        background-color: #F47920;
        font-size: 10px;
        flex-basis: 13%;
        height: 6.4em;
        img {
            font-size: 10px;
            width: 3em;
            height: 3em;
            object-fit: contain; } }
    input {
        border: none;
        flex-basis: 87%;
        background-color: #fff;
        padding: 16.5px 20px;
        color: #8D8D8D;
        font-weight: 400;
        font-size: 16px;
        line-height: 1.6;
        &::placeholder {
            color: #8D8D8D;
            font-weight: 400;
            font-size: 16px;
            line-height: 1.6; } } }
// Поле для ввода с select
.customer-input-group-select {
    overflow: hidden;
    position: relative;
    border: 1px solid #DFDEE2;
    border-radius: 5px;
    width: 100%;
    height: fit-content;
    &::after {
        content: "";
        position: absolute;
        display: block;
        width: 10px;
        height: 10px;
        border: 2px solid #8D8D8D;
        border-top: none;
        border-left: none;
        right: 22px;
        top: 22px;
        transform: rotate(45deg);
        pointer-events: none; }
    select {
        height: 64px;
        width: 100%;
        appearance: none;
        border: none;
        padding: 16.5px 20px;
        color: #8D8D8D;
        font-weight: 400;
        font-size: 16px;
        line-height: 1.6;
        cursor: pointer;
        &::placeholder {
            color: #8D8D8D;
            font-weight: 400;
            font-size: 16px;
            line-height: 1.6; } } }
// Раскрывающийся значок мусорного ведра
.label-garbage {
    position: absolute;
    right: 25px;
    top: 25px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    border-radius: 50px;
    color: #fff;
    display: flex;
    align-items: center;
    font-size: 0.875rem;
    font-weight: 500;
    padding: 8px 15px;
    background-color: #ff3838;
    transition: 0.2s all ease;
    border: none;
    &:hover {
        .label-garbage__span {
            display: flex;
            visibility: visible;
            opacity: 1; } }
    img {
        width: 20px;
        height: 20px;
        object-fit: contain; }
    &__span {
        margin-left: 10px;
        display: none;
        visibility: hidden;
        opacity: 0; } }
